/* global jQuery */
void (function initTabbedContent($) {
	'use strict';

	$(document).ready(() => {
		$('.tabbed-content .tabbed-content--tab').on('click', (event) => {
			const tabID = $(event.target).attr('data-toggle');

			$(event.target)
				.parents('.tabbed-content')
				.find('.tabbed-content--tab')
				.removeClass('tabbed-content--tab__active');
			$(event.target).addClass('tabbed-content--tab__active');

			$('.tabbed-content--panel').each((index, element) => {
				const panelID = $(element).attr('data-panel');

				if (tabID === panelID) {
					$(event.target)
						.parents('.tabbed-content')
						.find($(element))
						.addClass('tabbed-content--panel__active');
				} else {
					$(event.target)
						.parents('.tabbed-content')
						.find($(element))
						.removeClass('tabbed-content--panel__active');
				}
			});
		});
	});
})(jQuery);
